<template>
    <div class="lessonList">
        <div class="fixedArea" ref="fixedArea">
            <div class="header">
                <span @click="back" class="back">
                    <img src="../assets/img/back.png">
                </span>
                <span class="usersTitle">讲述课程</span>
                <span class="back"></span>
            </div>
            <div class="content">
                <div class="lessonIntroduce">
                    <div class="title">课程介绍</div>
                    <div class="introduce">{{ lesson.introduce }}</div>
                </div>
                <div class="lessonInfo">
                    <div class="title">课程内容</div>
                </div>
            </div>
        </div>
        <div class="content" :style="{marginTop: topHeight}" style="margin-bottom: 2.5rem;">
            <ul class="lessons">
                <li class="lesson" v-for="(info, index) in lesson.lessonInfo" :key="index">
                    <div class="cover">
                        <img :src="info.cover">
                    </div>
                    <div class="detail">
                        <span class="lessonName">{{ index + 1}} {{ info.lessonName }}</span>
                        <span class="space"></span>
                        <span class="playCounts">
                            <img src="../assets/img/playIcon.png" class="playIcon">
                            <span class="counts">{{ info.playCounts }}播放</span>
                        </span>
                    </div>
                </li>
            </ul>
        </div>
        <button class="buyBtn">价格：￥{{ lesson.price }}&emsp;立即购买</button>
    </div>
</template>

<script>
export default {
  name: 'lessonList',
  data () {
    return {
      topHeight: '',
      lesson: {
        introduce: '通过这些招式,能了解学习太极团的技术含义和要领。通过这些招式,能了解学习太极团的技术含义和要领。通过这些招式,能了解学习太极团的技术含义和要领。通过这些招式,能了解学习太极团的技术含义和要领。通过这些招式,能了解学习太极团的技术含义和要领。',
        price: '30.00',
        lessonInfo: [{
          cover: require('../assets/img/test.png'), lessonName: '起式', playCounts: '1.2万'
        }, {
          cover: require('../assets/img/test.png'), lessonName: '单鞭', playCounts: '1.2万'
        }, {
          cover: require('../assets/img/test.png'), lessonName: '白鹤亮翅', playCounts: '1.2万'
        }, {
          cover: require('../assets/img/test.png'), lessonName: '搂膝拗步', playCounts: '1.2万'
        }, {
          cover: require('../assets/img/test.png'), lessonName: '起式', playCounts: '1.2万'
        }, {
          cover: require('../assets/img/test.png'), lessonName: '单鞭', playCounts: '1.2万'
        }, {
          cover: require('../assets/img/test.png'), lessonName: '白鹤亮翅', playCounts: '1.2万'
        }, {
          cover: require('../assets/img/test.png'), lessonName: '搂膝拗步', playCounts: '1.2万'
        }]}
    }
  },
  mounted () {
    this.topHeight = this.$refs.fixedArea.getBoundingClientRect().height * 10 / window.screen.width + 0.32 + 'rem'
  },
  methods: {
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .fixedArea {
    position: fixed;
    left: 0;
    top: 0;
    background: #fff;
    z-index: 99;
    padding-bottom: 0.32rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    font-size: 0.48rem;
    background: #fff;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back,
  .next {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .content {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .lessonIntroduce {
    border-bottom: 1px solid #f5f5f5;
    padding-bottom: 0.32rem;
  }

  .title {
    text-align: start;
    font-size: 0.42667rem;
    margin-left: 0.21333rem;
    position: relative;
    z-index: -1;
  }

  .title::before {
    content: '';
    display: block;
    position: absolute;
    width: 0.05333rem;
    height: 100%;
    left: -0.21333rem;
    top: 0;
    background: #ffd200;
  }

  .introduce {
    font-size: 0.37333rem;
    color: #666666;
    margin-top: 0.37333rem;
    text-align: start;
  }

  .lessonInfo {
    margin-top: 0.37333rem;
  }

  .lesson {
    display: flex;
    align-items: center;
    margin: 0.32rem 0;
  }

  .cover {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3.2rem;
    height: 2.24rem;
  }

  .cover img {
    border-radius: 0.15rem;
  }

  .detail {
    flex-grow: 1;
    margin-left: 0.32rem;
  }

  .lessonName,
  .playCounts {
    display: inline-block;
    width: 100%;
    text-align: start;
  }

  .lessonName {
    font-size: 0.37333rem;
    color: #333333;
  }

  .playCounts {
    display: flex;
    align-items: center;
    font-size: 0.34667rem;
    color: #666666;
  }

  .space {
    display: block;
    height: 0.4rem;
    width: 100%;
  }

  .playIcon {
    width: 0.32rem;
    height: 0.32rem;
  }

  .counts {
    margin-left: 0.10667rem;
  }

  .buyBtn {
    width: 8.8rem;
    height: 1.36rem;
    position: fixed;
    bottom: 0.37333rem;
    left: calc(50% - 4.4rem);
    background: linear-gradient(to right, #ff8309, #ffd200);
    border: none;
    outline: none;
    border-radius: 0.15rem;
    color: #f5f5f5;
  }
</style>



